body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body{
  background-color: #2E0249;
  font-family: 'Montserrat', sans-serif;
}

hr{
  border:none;
  border-top:6px dotted #d2d2d285;
  width:14%;
}

.timeline {
    font-size: 16px;
    color: rgb(53, 53, 53);
    background-color: #32184e;
    /* max-height: 35em;
    overflow: auto; */
  }

  .fadingEffect{
    height: 35em;
    width: 100%;
    position: absolute;
    z-index: 1;
    pointer-events: none;
  }

.makeItCurve{
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

  .title {
    font-size: 2em;
    text-align: center;
    font-family: "Bebas Neue", sans-serif;
    color: aliceblue;
  }
  
  h3 {
    padding-top: 0.5em;
    margin: 10px;
  }
  
  .vertical-timeline-element-content {
    box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
      0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
    padding: 1em 3em 2em 2em !important;
  }
  
  .vertical-timeline-element-title{
    font-size: 25px;
  }

  .date {
    color: rgb(255, 255, 255);
  }

  #description {
    margin: 1em 0 1em 0;
    font-size: 15px;
  }

  #programWithButton {
    color: rgb(81, 81, 81);
    font-style: italic;
    margin: 1em 0 1em 0;
  }
  
  #program {
    color: rgb(81, 81, 81);
    font-style: italic;
  }

  .button {
    margin-top: 1em;
    text-decoration: none;
    padding: 0.5em 1em;
    border-radius: 5px;
    color: rgba(0, 0, 0, 0.753);
  }

  .workButton {
    background-color: #2cc7ff;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .workButton:hover {
    background-color: #2ab8ec;
  }
  
  .schoolButton {
    background-color: #f9c74f;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .schoolButton:hover {
    background-color: #f3bc3c;
  }

  @media only screen and (max-width: 1170px) {
    .vertical-timeline-element-date {
      display: block !important;
      float: none !important;
      color: rgb(255, 255, 255);
    }

    .vertical-timeline-element-content {
      padding: 1em 3em 0em 2em !important;
    }
  }
.name{
    padding: 2em;
    color: #37E2D5;
    font-family: 'Roboto Mono', monospace;
    font-size: 1em;
}

.nameBlock{
    margin: auto;
    height: 5em;
    width: 23em;
    background-color: rgba(255, 255, 255, 0.13);
}

.intro{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

@media only screen and (max-width: 360px) {
    .name{
        font-size: 0.88em;
    }    
    .nameBlock{
        margin: auto;
        height: 4.4em;
        width: 20em;
        background-color: rgba(255, 255, 255, 0.13);
    }
  }

  @media only screen and (max-width: 280px) {
    .name{
        font-size: 0.75em;
    }    
    .nameBlock{
        margin: auto;
        height: 3.8em;
        width: 20em;
        background-color: rgba(255, 255, 255, 0.13);
    }
  }
.projectSection {
    font-size: 16px;
    color: rgb(53, 53, 53);
    /* max-height: 35em;
    overflow: auto; */
  }

  .projectButton {
    background-color: #2cc7ff;
    margin-right: 2em;
  }
  
  .projectButton:hover {
    background-color: #2ab8ec;
  }
  

  .codeButton {
    background-color: #f9c74f;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  
  .codeButton:hover {
    background-color: #f3bc3c;
  }

  @media only screen and (max-width: 1170px) {
    .projectButton {
      display: block;
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;
      margin-bottom: 1em;
    }
  }
.footer{
    background-color: rgba(0, 0, 0, 0.307);
    text-align: center;
    color: rgb(221, 221, 221);
}

.social{
    color: rgba(233, 233, 233, 0.836);
    margin: 0.6em;
    border: solid #37e2d4af;
    border-width: 2px;
    border-radius: 100%;
    padding: 0.45em;
    width: 1.7em;
    height: 1.7em;
}

.linkedin{
    border-radius: 100%;
}

.credit{
    color: rgb(75, 75, 75);
    font-size: 0.8em;
}
.skills{
    display: flex;
    justify-content: center;
    align-items: center;
}

.skillTitle{
    color: #2cc7ff ;
    margin-bottom: 0.7em;
    font-size: 1.8em;
}

.skillDescription{
    margin: 0.8em;
    color: rgb(60, 60, 60) ;
    font-size: 1em ;
}

.skillBlock{
    margin: 2em;
    width: 18em;
    height: 18em;
    background-color: rgb(250, 250, 250);
    padding: 2em;
    border-radius: 2em;
    text-align: center;
}

@media only screen and (max-width: 1170px) {
    .skills{
        display: grid;
        justify-content: center;
        align-items: center;
    }
    .skillBlock{
        margin: 1em;
        width: 60vw;
        max-width: 17em;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        border-radius: 1.5em;
    }    
  }
.languagesection{
  background-color: #2a0242;
}
.dot {
    height: 1.5em;
    width: 1.5em;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    margin: 1.5em 0.5em;
  }

.fill {
    background-color: rgb(13, 164, 210);
  }

.languageBlock{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 0em;
}

p{
    margin: 0em;
}

  .language{
    color: white;
    font-size: 20px;
    width: 5em;
    display: inline-block;
    text-align: center;
  }

  .description{
    color: rgb(156, 156, 156);
    font-size: 13px;
    margin-top: 0.2em;
  }

  @media only screen and (max-width: 1170px) {
    .dot {
      height: 4vw;
      max-height: 1.5em;
      width: 4vw;
      max-width: 1.5em;
      margin: 1.2em 0.4em;
    }

    .language{
      font-size: 18px;
    }
    .languageBlock{
      margin-right: 0.5em;
  }
  }

  @media only screen and (max-width: 420px) {
    .dot {
      margin: 1.2em 0.25em;
    }
  }
